import { useEffect, useState } from "react";
import axios from "axios";

function useFetch(request, hash, identifier) {
    const baseURL = "https://hook.doo.integromat.celonis.com/" + localStorage.getItem("webhookURL");

    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    useEffect(() => {
        if (request) {
            setLoading(true);
            console.log("Fetch: " + request + hash + identifier);
            axios
                .get(`${baseURL}?request=${request}&hash=${hash}&identifier=${identifier}`)
                .then((response) => {
                    setData(response.data);
                    console.log(response.data);
                })
                .catch((err) => {
                    setError(err);
                })
                .finally(() => {
                    setLoading(false);
                });
        }
    }, [request, hash, identifier, baseURL]);

    function refetch(request, hash, identifier) {
        setLoading(true);
        axios
            .get(`${baseURL}?request=${request}&hash=${hash}&identifier=${identifier}`)
            .then((response) => {
                console.log(response.data);
                setData(response.data);
            })
            .catch((err) => {
                setError(err);
            })
            .finally(() => {
                setLoading(false);
            });
    }

    return { data, loading, error, refetch, setData };
}

export default useFetch;
