import React from "react";
import { Grid, Box } from "@mui/material";
import StatsTickets from "./StatsTickets";
import StatsEvent from "./StatsEvent";
import StatsTimeline from "./StatsTimeline";
import useFetch from "../../hooks/useFetch";
import StatsNumber from "./StatsNumber";
import HowToRegIcon from "@mui/icons-material/HowToReg";

function Stats() {
    const { data, loading, error } = useFetch(
        "stats",
        undefined,
        localStorage.getItem("eid")
    );

    if (error) console.log(error);

    return (
        <Box>
            <Grid container spacing={3}>
                <Grid container item xs={12} lg={2} spacing={3}>
                    <Grid item xs={6} lg={12}>
                        <StatsNumber
                            loading={loading}
                            number={data?.checkedInAttendees}
                            description="Check Ins"
                            icon={
                                <HowToRegIcon
                                    color="secondary"
                                    fontSize="large"
                                />
                            }
                        />
                    </Grid>
                    <Grid item xs={6} lg={12}>
                        <StatsNumber
                            loading={loading}
                            number={data?.allAttendees}
                            description="Registrierungen"
                            icon={
                                <HowToRegIcon
                                    color="secondary"
                                    fontSize="large"
                                />
                            }
                            sx={{ height: 1 }}
                        />
                    </Grid>
                </Grid>
                <Grid item xs={12} lg={10}>
                    <StatsTimeline data={data} />
                </Grid>
                <Grid item xs={12} lg={8}>
                    <StatsTickets data={data} loading={loading} />
                </Grid>
                <Grid item xs={12} lg={4}>
                    <StatsEvent data={data} loading={loading} />
                </Grid>
            </Grid>
        </Box>
    );
}

export default Stats;
